import { mapGetters } from 'vuex'
export default {
  middleware: 'auth',
  layout: 'none',
  data () {
    return {
      error: '',
      stripe: null,
      card: null
    }
  },

  computed: {
    ...mapGetters([
      'getCart',
      'getCartTotalPriceWithTotalPrice',
      'getEstimatedDelivery'
    ])
  },

  mounted () {
    this.stripe = Stripe(process.env.STRIPE)
    let elements = this.stripe.elements()
    this.card = elements.create('card')
    this.card.mount(this.$refs.card)
  },

  methods: {
    async onPurchase () {
      try {
        let token = await this.stripe.createToken(this.card)
        if (!token.error) {
          let response = await this.$axios.$post(`${process.env.DEV_BACKEND}/api/payment/pay`, {
        token: token,
        totalPrice: this.getCartTotalPriceWithTotalPrice,
        cart: this.getCart,
        estimatedDelivery: this.getEstimatedDelivery
      })

         if (response.success) {
            this.$store.commit('clearCart')
             this.$router.push('/')
          }
        } else {
          this.error = token.error
        }
        
      } catch (error) {
        this.error = error
        console.log(error);
      }
    }
  }
}
